const { app, BrowserWindow, Tray, Menu, globalShortcut, nativeImage} = require('electron');

// Handle creating/removing shortcuts on Windows when installing/uninstalling.
if (require('electron-squirrel-startup')) { // eslint-disable-line global-require
  app.quit();
}


const path = require('path');
var iconPath = path.join(__dirname, '/assets/tray.png')

let trayIcon = nativeImage.createFromPath(iconPath);
//var tokenNative = require('./token.node');
var logoPath = path.join(__dirname, '/assets/logo.icns')
let firstBlur = true;
/** 
require('update-electron-app')({
  repo: 'asilluron/gif-cake',
  updateInterval: '1 hour',
  logger: require('electron-log')
})*/


// Keep a global reference of the window object, if you don't, the window will
// be closed automatically when the JavaScript object is garbage collected.
let mainWindow;

const createWindow = () => {
  // Create the browser window.
  mainWindow = new BrowserWindow({
    width: 600,
    height: 300,
    frame: false,
    resizable: false,
    icon: logoPath,
    webPreferences: {
      nodeIntegration: true
    }
  });


  //global.token = tokenNative.token();


  globalShortcut.register('CommandOrControl+Alt+G', () => {
    mainWindow.show();
  })

  globalShortcut.register('Esc', () => {
    mainWindow.hide();
  })

  mainWindow.on('blur', () => {
    if(firstBlur) {
      firstBlur = false;
    } else {
      mainWindow.hide();
    }
  });


  // and load the index.html of the app.
  mainWindow.loadURL(MAIN_WINDOW_WEBPACK_ENTRY);
  tray = new Tray(__dirname + nativeImage.createFromPath('./build/tray.png'))



  const contextMenu = Menu.buildFromTemplate([
    { label: 'Quit', type: 'normal', role: 'quit' }
  ])
  tray.setToolTip('Gif Cake')
  tray.setContextMenu(contextMenu)
  
  app.dock.hide()

  // Emitted when the window is closed.
  mainWindow.on('closed', () => {
    // Dereference the window object, usually you would store windows
    // in an array if your app supports multi windows, this is the time
    // when you should delete the corresponding element.
    mainWindow = null;
  });
};

// This method will be called when Electron has finished
// initialization and is ready to create browser windows.
// Some APIs can only be used after this event occurs.
app.on('ready', createWindow);
// Quit when all windows are closed.
app.on('window-all-closed', () => {
  // On OS X it is common for applications and their menu bar
  // to stay active until the user quits explicitly with Cmd + Q
  if (process.platform !== 'darwin') {
    app.quit();
  }
});



app.on('activate', () => {
  // On OS X it's common to re-create a window in the app when the
  // dock icon is clicked and there are no other windows open.
  if (mainWindow === null) {
    createWindow();
  }
});

